* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
}

main {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem;
  max-width: 600px;
}

main h1 {
  font-size: 3rem;
  color: brown;
}

.container {
  margin-top: 5rem;
  padding: 2rem 1rem;
  background-color: whitesmoke;
  border-radius: 10px;
  box-shadow: 0 0 5px 8px rgb(195, 194, 194);
}

.review {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  background-color: whitesmoke;
}

.review h3,
.review h4 {
  text-transform: capitalize;
}

.review h4 {
  font-size: 1.5rem;
}

.review h3 {
  font-size: 1.3rem;
  color: rgb(0, 252, 168);
}

img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
}

.info {
  margin-bottom: 1.5rem;
  text-indent: 1.5rem;
  font-size: 1.2rem;
}

button {
  user-select: none;
}

.btn-container {
  text-align: center;
  background-color: whitesmoke;
}

.next {
  margin-left: 2rem;
  font-size: 1.6rem;
  color: rgb(20, 20, 227);
  cursor: pointer;
}

.previous {
  font-size: 1.6rem;
  color: rgb(20, 20, 227);
  cursor: pointer;
}

.surprise-btn {
  margin: 1.5rem auto;
  display: block;
  background-color: orangered;
  color: white;
  border: none;
  outline: none;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
}
